.nav-head {
    width: 100vw;
    padding: 1rem;
    /* background-color: #1a2b3c; */
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.nav-logo {
    /* color: whitesmoke; */
    font-size: 1.8rem;
    font-weight: 700;
}

.nav-input {
    padding: .8rem;
    width: 20rem;
    border-radius: 5px;
}

.nav-input-boxes {
    width: 0;
    height: 0;
    overflow: hidden;
}

@media all and (max-width:650px) {
    .nav-input-box {
        width: 0;
        height: 0;
        overflow: hidden;
    }

    .nav-input-boxes {
        width: 100%;
        height: auto;
        overflow: visible;
        padding: 1rem;
        display: flex;
        justify-content: center;
    }
}